import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { Helmet } from 'react-helmet'
import './assets/css/EventStaffing.scss'
import eventIcon from './assets/images/event-icon.svg'
import bannerImg from './assets/images/banner.jpg'
import bannerImg2 from './assets/images/bg2.jpg'
import shortlistImg from './assets/images/shortlist.jpg'
import howIWork1 from './assets/images/how-it-work.png'
import howIWork2 from './assets/images/how-it-work2.png'
import quote from './assets/images/quote.svg'
import icon1 from './assets/images/icon1.png'
import icon2 from './assets/images/icon2.png'
import icon3 from './assets/images/icon3.png'
import icon4 from './assets/images/icon4.png'
import Banner from './Components/Banner/Banner'
import CarouselLogs from './Components/CarouselLogs/CarouselLogs'
import ColumnSection from './Components/ColumnSection/ColumnSection'
import Card from './Components/Card/Card'
import mixpanel from 'mixpanel-browser'

const EventStaffing = ({t}) => {

  React.useEffect(() => {
    mixpanel.track('Page View - Event Staffing');
  }, []);

  return (
    <div>
      <Helmet>
        <title>Event Staffing</title>
        <meta name='description' content='' />
        <meta property='og:type' content='website' />
        <meta property='og:image' content='' />
        <meta property='og:image:width' content='1200' />
        <meta property='og:image:height' content='630' />
        <meta property='og:url' content='https://oliv.com' />
        <meta property='og:title' content='Event Staffing - Oliv' />
        <meta property='og:description' content='Learn more about Event Staffing' />
      </Helmet>

      <Banner
        style={1}
        bannerImg={bannerImg}
        eventIcon={eventIcon}
        heading={t('eventStaffing.hero.heading')}
        des={t('eventStaffing.hero.des')}
      />

      <CarouselLogs
        heading={t('eventStaffing.CarouselLogs.heading')}
      />

      <ColumnSection
        isReverseColumn={false} // row-reverse
        heading={t('eventStaffing.howItWorks.heading')}
        contentSlogan={t('eventStaffing.howItWorks.content1.slogan')}
        contentHeading={t('eventStaffing.howItWorks.content1.heading')}
        contentDes1={t('eventStaffing.howItWorks.content1.des1')}
        contentDes2={t('eventStaffing.howItWorks.content1.des2')}
        image={howIWork1}
      />

      <ColumnSection
        isReverseColumn
        contentSlogan={t('eventStaffing.howItWorks.content2.slogan')}
        contentHeading={t('eventStaffing.howItWorks.content2.heading')}
        contentDes1={t('eventStaffing.howItWorks.content2.des1')}
        image={howIWork2}
        className='custom-column'
      />

      <Banner
        style={2}
        bannerImg={shortlistImg}
        slogan={t('eventStaffing.shortlist.slogan')}
        heading={t('eventStaffing.shortlist.heading')}
        listContent={
          {
            list: [
              {
                image: icon1,
                title: t('eventStaffing.shortlist.list1.title'),
                des: t('eventStaffing.shortlist.list1.des')
              },
              {
                image: icon2,
                title: t('eventStaffing.shortlist.list2.title'),
                des: t('eventStaffing.shortlist.list2.des')
              },
              {
                image: icon3,
                title: t('eventStaffing.shortlist.list3.title'),
                des: t('eventStaffing.shortlist.list3.des')
              },
              {
                image: icon4,
                title: t('eventStaffing.shortlist.list4.title'),
                des: t('eventStaffing.shortlist.list4.des')
              }
            ]
          }
        }
      />

      <Card
        icon={quote}
        des={t('eventStaffing.quote.client1.des')}
        name={t('eventStaffing.quote.client1.name')}
        designation={t('eventStaffing.quote.client1.designation')}
      />

      <Banner
        style={3}
        bannerImg={bannerImg2}
        heading={t('eventStaffing.videoSection.heading')}
        des={t('eventStaffing.videoSection.des')}
        videoUrl='https://www.youtube.com/watch?v=f9XEhUPRNTM'
        className='hero-section__video'
      />

      <Card
        className='cta_section'
        heading={t('eventStaffing.ctaSection.heading')}
        buttons={
          {
            button: [
              {
                name: `Let's talk `,
                btnAction: () => { window.location.href = '/contact'; mixpanel.track('Button Click - Lets Talk - Event Staffing'); }
              }
            ]
          }
        }
      />

    </div>
  )
}

export default withTranslations(EventStaffing)



