import React from 'react'
import Container from 'Shared/components/Container'
import './ColumnSection.scss'

const ColumnSection = (props) => {
  const {isReverseColumn, heading, contentSlogan, contentHeading, contentDes1, contentDes2, image, className} = props

  return (
    <div className={`column-section__eventStaffing-page ${className}`}>
      <Container>
        <h1 className='heading'>{heading}</h1>
        <div className='columns' style={{flexDirection: `${isReverseColumn ? 'row-reverse' : 'row'}`}}>
          <div className='column col-4 col-xs-12'>
            <div className='content'>
              <h4>{contentSlogan}</h4>
              <h2>{contentHeading}</h2>
              <p>{contentDes1}</p>
              <p>{contentDes2}</p>
            </div>
          </div>
          <div className='column col-8 col-xs-12'>
            <div className='image'>
              <img src={image} />
            </div>
          </div>
        </div>
      </Container>
    </div>
  )
}

export default ColumnSection



