import React from 'react'
import Container from 'Shared/components/Container'
import './Card.scss'
import Button from 'Shared/components/Button'

const Card = (props) => {
  const {icon, des, name, designation, className, buttons, heading} = props
  return (
    <div className={`quote-section__eventStaffing-page ${className}`}>
      <Container>
        <div className='section'>
          {icon && <img src={icon} />}
          {des && <p>{des}</p>}
          {name && <h3>{name}</h3>}
          {designation && <h6>{designation}</h6>}
          {heading && <h2>{heading}</h2>}
          {buttons &&
          <div className='list_content' >
            {buttons.button.map((item, index) => {
              return (
                <div className='button_div' key={index}>
                  <Button className='button button--primary' onClick={item.btnAction}>{item.name}</Button>
                </div>
              )
            })}
          </div>
          }
        </div>
      </Container>
    </div>
  )
}

export default Card



