import React from 'react'
import Container from 'Shared/components/Container'
import './CarouselLogs.scss'
import {clientLogo1, clientLogo2, clientLogo3, clientLogo4, clientLogo5, clientLogo6, clientLogo7} from './../../assets/images/client-logos/index'

const CarouselLogs = (props) => {
  const {heading} = props

  const logos = [clientLogo1, clientLogo2, clientLogo3, clientLogo4, clientLogo5, clientLogo6, clientLogo7]
  return (
    <div className='carousel-logos-section__eventStaffing-page'>
      <Container>
        <h1>{heading}</h1>
        <div className='client_logos'>
          {logos.map((item, index) => {
            return (
              <img key={index} src={item} />
            )
          })}
        </div>
      </Container>
    </div>
  )
}

export default CarouselLogs



