import React, {useState} from 'react'
import Container from 'Shared/components/Container'
import Button from 'Shared/components/Button'
import './Banner.scss'
import ReactPlayer from 'Shared/components/AsyncReactPlayer'
import mixpanel from 'mixpanel-browser'

const Banner = (props) => {
  const {bannerImg, eventIcon, heading, des, style, slogan, listContent, videoUrl, className} = props

  const [open, setOpen] = useState(false)

  const openVideoPlayer = () => {
    setOpen(true)
  }

  const closeVideoPlayer = () => {
    setOpen(false)
  }

  return (
    <div className={`hero-section__eventStaffing-page ${className} ${open && 'videoShowDiv'}`} style={{backgroundImage: `url(${bannerImg})`}}>
      <Container>
        {style === 1 ? <div className='hero-section_content'>
          <img src={eventIcon} />
          <h1>{heading}</h1>
          <p>{des}</p>
          <Button className='button button--primary btnBanner' onClick={() => { window.location.href = '/contact'; mixpanel.track('Button Click - Lets Talk - Event Staffing'); }}>Let's talk </Button>
        </div> : style === 2 ? <div className='hero-section_contentList'>
          <h3>{slogan}</h3>
          <h2>{heading}</h2>

          {listContent &&
          <div className='list_content' >
            {listContent.list.map((item, index) => {
              return (
                <div className='list' key={index}>
                  <img src={item.image} />
                  <div className='content_des'>
                    <h3>{item.title}</h3>
                    <p>{item.des}</p>
                  </div>
                </div>
              )
            })}
          </div>
          }
        </div> : style === 3 ? <div className='hero-section_videoSection'>
          <h1>{heading}</h1>
          <p>{des}</p>
          <Button className='button button--primary' onClick={openVideoPlayer}>Watch Video</Button>
          {open &&
          <div className='hero-section__videoPalyer'>
            <button onClick={closeVideoPlayer}>
              <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 50 50' width='100px' height='100px'><path d='M 7.71875 6.28125 L 6.28125 7.71875 L 23.5625 25 L 6.28125 42.28125 L 7.71875 43.71875 L 25 26.4375 L 42.28125 43.71875 L 43.71875 42.28125 L 26.4375 25 L 43.71875 7.71875 L 42.28125 6.28125 L 25 23.5625 Z' /></svg>
            </button>
            <ReactPlayer
              url={videoUrl}
              width='100%'
              controls
              playing
              height='100%'
              className='react-player'
            />
          </div>
          }
        </div> : ''}
      </Container>

    </div>
  )
}

export default Banner



